/* ––––––––––––––––––––––––––––––––––––––––––––––––––
BREADCRUMBS
–––––––––––––––––––––––––––––––––––––––––––––––––– */

.breadcrumbs {
    display: block;
    overflow: hidden;
    margin: 0;
    list-style: none;

    li {
        float: left;
        margin: 0;
        padding: 0 .75em 0 0;

        a {
            padding-left: .75em;
        }

        &:before {
            position: relative;
            content: '\002f';
            color: $breadcrumbs-divider-color;
        }

        &:first-child {

            a {
                padding-left: 0;
            }

            &:before {
                content: ' ';
            }

        }

    }

    .current {

        a {
            padding-left: .75em;
            color: $base-font-color;
            cursor: default;
        }

    }

}