/* ––––––––––––––––––––––––––––––––––––––––––––––––––
TYPOGRAPHY
–––––––––––––––––––––––––––––––––––––––––––––––––– */

h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
    margin-top: $baseline;
    margin-bottom: $baseline;
    padding: 0;
    color: $heading-font-color;
    font-weight: normal;
    font-family: $heading-font;

    a {
        font-weight: inherit;
    }

}

h1, .h1 {
    font-size: $h1-font-size;
    line-height: 1.2;
}

h2, .h2 {
    font-size: $h2-font-size;
    line-height: 1.25;
}

h3, .h3 {
    font-size: $h3-font-size;
    line-height: 1.3;
}

h4, .h4 {
    font-size: $h4-font-size;
    line-height: 1.35;
}

h5, .h5 {
    font-size: $h5-font-size;
    line-height: 1.5;
}

h6, .h6 {
    font-size: $h6-font-size;
    line-height: $base-line-height;
}

p {
    margin-top: 1em;
    margin-bottom: 1em;
}

b,
strong {
    font-weight: bold;
}

i,
em {
    color: $italic-font-color;
    font-style: italic;
}

u {
    text-decoration: underline;
}

sup,
sub {
    position: relative;
    height: 0;
    vertical-align: baseline;
    font-size: $small-font-size;
    line-height: .5;
    _vertical-align: bottom;
}

sup {
    bottom: .5rem;
}

sub {
    top: .5rem;
}

small {
    font-size: $small-font-size;
}

abbr[title],
abbr[data-original-title] {
    border-bottom: 1px dotted $abbr-border-bottom-color;
    text-decoration: none;
    cursor: help;
}

code {
    background: #f1f1f1;
    padding: .2em .5em;
    margin: 0 .2rem;
    white-space: nowrap;
}

pre {
    background: #f1f1f1;
    font-family: 'Menlo', 'Consolas', 'Bitstream Vera Sans Mono', 'DejaVu Sans Mono', 'Monaco', monospace;

    & > code {
        background: transparent;
        border-radius: 0;
        display: block;
        padding: 1em;
        white-space: pre;
    }

}

.text-highlight {
    position: relative;
    padding: .1em .3em;
    background: $base-font-color;
    color: $white;
}

/* ––––––––––––––––––––––––––––––––––––––––––––––––––
BLOCKQUOTES
–––––––––––––––––––––––––––––––––––––––––––––––––– */

blockquote {
    margin: ($baseline*2) 0;
    padding: ($baseline/2) 2em;
    border-left: solid 4px $blockqoute-border-color;
    color: $blockqoute-color;
    font-style: italic;

    p {
        margin: 0;
    }

    cite {
        display: block;
        margin-top: 1em;

        &:before {
            content: '\2014 \0020';
        }

    }

}

/* ––––––––––––––––––––––––––––––––––––––––––––––––––
SIZE HELPERS
–––––––––––––––––––––––––––––––––––––––––––––––––– */

i {

    &.x-lg {
        vertical-align: -15%;
        font-size: 1.33333333em;
        line-height: .75em;
    }

    &.x-2 {
        font-size: 2em;
    }

    &.x-3 {
        font-size: 3em;
    }

    &.x-4 {
        font-size: 4em;
    }

    &.x-5 {
        font-size: 5em;
    }

    &.icon-fixed {
        width: 1.28571429rem;
        text-align: center;
    }

    &.icon-margin-right {
        margin-right: 1em;
    }

    &.icon-margin-left {
        margin-left: 1em;
    }

    &.icon-middle {
        vertical-align: middle;
    }

}

p {

    &.x-lg {
        font-size: 1.5em;
    }

    &.x-2 {
        font-size: 2em;
    }

    &.x-3 {
        font-size: 3em;
    }

    &.x-4 {
        font-size: 4em;
    }

    &.x-5 {
        font-size: 5em;
    }

}