/* ––––––––––––––––––––––––––––––––––––––––––––––––––
PADDING'S
–––––––––––––––––––––––––––––––––––––––––––––––––– */

.no-padding {
    padding: 0;
}

.padding-top {
    padding-top: $normal-spacer;
}

.padding-top-double {
    padding-top: $double-spacer;
}

.padding-top-half {
    padding-top: $half-spacer;
}

.padding-bottom {
    padding-bottom: $normal-spacer;
}

.padding-bottom-double {
    padding-bottom: $double-spacer;
}

.padding-bottom-half {
    padding-bottom: $half-spacer;
}

.padding-top-bottom {
    padding-top: $normal-spacer;
    padding-bottom: $normal-spacer;
}

.padding-top-bottom-double {
    padding-top: $double-spacer;
    padding-bottom: $double-spacer;
}

.padding-top-bottom-half {
    padding-top: $half-spacer;
    padding-bottom: $half-spacer;
}

.padding-left-right {
    padding-right: $normal-spacer;
    padding-left: $normal-spacer;

    &[class^="span-"] {
        padding-right: $normal-spacer;
        padding-left: $normal-spacer;
    }

}

.padding-left-right-double {
    padding-right: $double-spacer;
    padding-left: $double-spacer;

    &[class^="span-"] {
        padding-right: $double-spacer;
        padding-left: $double-spacer;
    }

}

.padding-left-right-half {
    padding-right: $half-spacer;
    padding-left: $half-spacer;

    &[class^="span-"] {
        padding-right: $half-spacer;
        padding-left: $half-spacer;
    }

}

.padding-all {
    padding: $normal-spacer;

    &[class^="span-"] {
        padding: $normal-spacer;
    }

}

.padding-all-double {
    padding: $double-spacer;

    &[class^="span-"] {
        padding: $double-spacer;
    }

}

.padding-all-half {
    padding: $half-spacer;

    &[class^="span-"] {
        padding: $half-spacer;
    }

}

