/* ––––––––––––––––––––––––––––––––––––––––––––––––––
BASE
–––––––––––––––––––––––––––––––––––––––––––––––––– */

html {
    @include box-sizing;
    overflow: -moz-scrollbars-vertical;
    overflow-y: scroll;
    min-height: 100%;
    height: 100%;
    font-size: 62.5%;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-rendering: optimizeLegibility;

    @include breakpoint(sm) {
        font-size: 55%;
    }

}

body {
    margin: 0;
    padding: 0;
    min-height: 100%;
    height: 100%;
    background: $body-background;
    color: $base-font-color;
    letter-spacing: $base-letter-spacing;
    font-size: $base-font-size;
    font-family: $base-font;
    line-height: $base-line-height;
}

* {
    box-sizing: inherit;

    &:before,
    &:after {
        box-sizing: inherit;
    }

}

::-moz-selection {
    background: $selection-color;
    text-shadow: none;
}

::selection {
    background: $selection-color;
    text-shadow: none;
}

::-webkit-selection {
    background: $selection-color;
    text-shadow: none;
}

::-ms-selection {
    background: $selection-color;
    text-shadow: none;
}

::-m4r10-l0nc4rek {
    color: #BADA55;
    display: flex;
}

input,
select,
textarea,
button {

    &:focus {
        outline: none;
    }

}