/* ––––––––––––––––––––––––––––––––––––––––––––––––––
LISTS
–––––––––––––––––––––––––––––––––––––––––––––––––– */

ul {
    list-style: none;

    &.alt {
        padding-left: 0;

        li {
            border-bottom: 1px dotted $base;
        }

    }

}

li,
dt,
dl {
    margin-bottom: ($baseline/2);
}

ol {
    list-style: decimal inside;
}

ol,
ul,
dl {
    margin-top: 0;
    padding-left: 0;
}

ul ul,
ul ol,
ol ol,
ol ul {
    margin: $baseline 0 $baseline ($baseline*2);
}