/* ––––––––––––––––––––––––––––––––––––––––––––––––––
MEDIA
–––––––––––––––––––––––––––––––––––––––––––––––––– */

img {
    position: relative;
    margin: 0;
    padding: 0;
    vertical-align: middle;

    &:before {
        content: " ";
        display: block;
        position: absolute;
        height: 100%;
        width: 100%;
        background-color: $image-not-found-background;
        border: 1px dashed $image-not-found-border;
    }

    &:after {
        @include center(vertical);
        content: attr(alt) ' - not found';
        color: $image-not-found-color;
        display: block;
        width: 100%;
        text-align: center;
        margin-bottom: 1rem;
    }

    &.img-left {
        float: left;
        margin: 0 1em 1em 0;
        border: none;
    }

    &.img-right {
        float: right;
        margin: 0 0 1em 1em;
        border: none;
    }

    &.img-responsive {
        width: 100%;
        height: auto;
    }

    &.img-border-thumbnail {
        margin: 1em 0;
        padding: .3rem;
        border: 1px solid $base;
    }

    &.img-border-thumbnail-2 {
        @include border-radius-radius;
        margin: 1em 0;
        padding: .8rem;
        box-shadow: 1px 2px 2px rgba(0, 0, 0, .25);
    }

    &.img-border-thumbnail-3 {
        @include border-radius-radius(8px);
        margin: 1em 0;
        padding: .4rem;
        box-shadow: 0 1px 2px rgba(164, 158, 147, .6);
    }

    &.img-border-line {
        @include border-radius-round;
        margin: 1em 0;
        padding: 1rem;
        border: 1px solid $base;
    }

    &.img-border-flat {
        margin: 1em 0;
        border: 10px solid $base;
    }

}

.media {
    position: relative;
    overflow: hidden;
    margin: 0 0 $baseline;
    padding-top: $baseline;
    padding-bottom: 51%;
    height: 0;
    border: 1px solid $base;

    iframe,
    object,
    embed {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border: 0;
    }

}

figure {
    display: table;
    width: 100%;
    margin: 0;

    img {
        width: 100%;
    }

}

figcaption {
    margin-top: 1em;
    font-style: italic;
}