/* ––––––––––––––––––––––––––––––––––––––––––––––––––
BADGES
–––––––––––––––––––––––––––––––––––––––––––––––––– */

.badge {
    display: inline-block;
    padding: .5rem .7rem;
    background: $default-badge-background;
    color: $white;
    text-align: center;
    white-space: nowrap;
    font-size: $small-font-size;
    line-height: 1;
    cursor: pointer;

    &-right {
        float: right;
    }

    &-outline {
        border: 1px solid $default-badge-background;
        background: none;
        color: $default-badge-background;
        margin-top: -1px;
    }

    &-blank {
        background: none;
        color: $default-badge-background;
    }

}